.PortfolioPage{

}
.portfolis{
    width: 100%;
    display: grid;
    
    grid-template-columns: repeat(1, 1fr);
    row-gap: 2rem;
    column-gap: 1rem;
    @media screen and (max-width: 1600px){
        grid-template-columns: repeat(1, 1fr);
    }
    @media screen and (max-width: 900px){
        grid-template-columns: repeat(1, 1fr);
    }
    .portfolio{
        border: 2px dotted white; 
        
        .image-data{
            position: relative;
            &::before{
                content: "";
                position: absolute;
                top: 12px;
                left: 15px;
                height: calc(100% - 30px);
                width: calc(100% - 30px);
                background-color: rgba(0, 0, 0, 0.3);
                transform-origin: center;
                transform: scale(0);
                transition: all .4s ease-in-out;

            }
            &:hover::before{
                transform: scale(1);
            }
            img{
                width: 100%;
                height: 30vh;
                object-fit: cover;
            }

            .hover-items{
                
                list-style: none;
                position: absolute;
                top: 70%;
                left: 50%;
                transform: translate(-50%, -50%);
                padding: 1rem 2rem;
                color: red;
                visibility: hidden;
                li{
                    a{
                        padding: 1rem;
                        font-size: 100px;
                        text-decoration: none;
                        font-family: inherit;
                        border-radius: 10px;
                        color:violet;

                        &:hover{
                            color:  #037FFF;
                            cursor: pointer;
                        }
                        &:not(:last-child){
                            margin-right: 2rem;
                        }
                    }
                }
            }

            &:hover{
                .hover-items{
                    
                    color: red;
                    visibility: visible;
                    top: 50%;
                }
            }
        }
        
        h5{
            font-size: 1.9rem;
            font-weight: bolder;
            color: yellow;

            transition: all .3s ease-in-out;
            &:hover{
                color:  #037FFF;
                cursor: pointer;
            }
        }
        p{
            color: violet;
            font-family: inherit;
            font-size: 1.3rem;
        }
    }
}